export default {
	created() {
		this.getLocation()
	},
	data() {
		return {
			latitude: '',
			longitude:''
		}
	},
	methods: {
		bindMap() {
			uni.navigateTo({
				url: '../../pages/index/map'
			})
		},
		getLocation(){
			uni.getLocation({
			    type: 'wgs84',
			    success: res=> {
					console.log(res)
					this.latitude = res.latitude
					this.longitude = res.longitude
				}
			})
		}
	},
}
